<template>
  <div>
    <div class="profile_header">
      <div class="profile_title">
        <router-link to="/dashboard/employees" class="profile__move_back"></router-link>
        <span>{{ employeeFindedData.username }}</span>
      </div>
      <PersonPannel></PersonPannel>
    </div>
    <ProfileCard :data="employeeProfileCardData"></ProfileCard>
    <div class="profile_data_box" v-if="!openedSettings">
      <span class="profile_data__item">Username:<span>{{ employeeFindedData.username }}</span></span>
      <span class="profile_data__item">Email address:<span>{{ employeeFindedData.email }}</span></span>
      <span class="profile_data__item">Contact phone:<span>{{ employeeFindedData.phone }}</span></span>
      <AppButton text="Change settings" :type="'default'" @action="openedSettings = true"></AppButton>
    </div>
    <form action="" class="profile_settings" v-if="openedSettings">
      <TextInput
        @emitTextInput="settingsUsernameChange"
        label="Username"
        placeholder="Type name here..."
        inputId="profileSettingsUsernamme"
        :model="employeeFindedData.username"
        ></TextInput>
      <TextInput
        @emitTextInput="settingsEmailChange"
        label="Email"
        placeholder="Type email here..."
        inputId="profileSettingsEmail"
        :model="employeeFindedData.email"
        ></TextInput>
      <TextInput
        @emitTextInput="settingsPhoneChange"
        label="Contact phone"
        placeholder="Type phone here..."
        inputId="profileSettingsPhone"
        :model="employeeFindedData.phone"
        ></TextInput>
        <TextInput
        @emitTextInput="settingsTokensChange"
        label="Tokens"
        placeholder="..."
        inputId="profileSettingsTokens"
        :model="employeeFindedData.tokens"
        ></TextInput>
        <TextInput
        @emitTextInput="settingsPayloadChange"
        label="Credits"
        placeholder="..."
        inputId="profileSettingsCredits"
        :model="employeeFindedData.credits"
        ></TextInput>
      <AppButton text="save change" :type="'danger'" @action="saveChangeEmployeeProfile({
        username: changeParamModel (settingsUsernameModel, employeeFindedData.username),
        email: changeParamModel (settingsEmailModel, employeeFindedData.email),
        phone: changeParamModel (settingsPhoneModel, employeeFindedData.phone),
        tokens: changeParamModel (cutFirstZero(settingsTokensModel), employeeFindedData.tokens),
        credits: changeParamModel (cutFirstZero(settingsCreditsModel), employeeFindedData.credits),
        id: this.$route.params.id
      })"></AppButton>
    </form>
  </div>
</template>

<script>
import { mapMutations, mapState } from 'vuex'

import ProfileCard from '@/components/ProfileCard.vue'
import AppButton from '@/components/AppButton.vue'
import TextInput from '@/components/TextInput.vue'
import PersonPannel from '@/components/PersonPannel.vue'
export default {
  data () {
    return {
      openedSettings: false,
      settingsUsernameModel: '',
      settingsEmailModel: '',
      settingsPhoneModel: '',
      settingsTokensModel: '',
      settingsCreditsModel: ''
    }
  },
  computed: {
    ...mapState({
      stateEmployeesData: 'employeesData'
    }),
    employeeFindedData () {
      return this.stateEmployeesData.find((e) => String(e.id) === this.$route.params.id)
    },
    employeeProfileCardData () {
      return {
        avatar: '',
        username: this.employeeFindedData.username,
        credits: this.employeeFindedData.credits,
        tokens: this.employeeFindedData.tokens,
        status: this.employeeFindedData.status
      }
    }
  },
  methods: {
    ...mapMutations(['changeEmployeeDataState']),
    changeParamModel (model, value) {
      if (model === '') {
        return value
      } else {
        return model
      }
    },
    cutFirstZero (str) {
      if (str.length >= 1) {
        return str
      } else {
        return str.startsWith('0') ? str.slice(1) : str
      }
    },
    settingsUsernameChange (data) {
      this.settingsUsernameModel = data
    },
    settingsEmailChange (data) {
      this.settingsEmailModel = data
    },
    settingsPhoneChange (data) {
      this.settingsPhoneModel = data
    },
    settingsTokensChange (data) {
      this.settingsTokensModel = data
    },
    settingsPayloadChange (data) {
      this.settingsCreditsModel = data
    },
    saveChangeEmployeeProfile (data) {
      this.changeEmployeeDataState({
        username: data.username,
        email: data.email,
        phone: data.phone,
        tokens: data.tokens,
        credits: data.credits,
        id: data.id
      })
      this.openedSettings = false
    }
  },
  components: {
    ProfileCard,
    AppButton,
    TextInput,
    PersonPannel
  }
}
</script>
