<template>
  <div>
    <label :for="inputId">{{ label }}</label>
    <input
      v-model.trim="inputModel"
      @change="emitData"
      type="text"
      :id="inputId"
      :placeholder="placeholder"
      :invalid="invalid"
      :class="invalid ? 'danger-text-input' : ''"
      :model="model"
      >
  </div>
</template>

<script>
export default {
  props: {
    label: String,
    placeholder: String,
    inputId: String,
    model: {
      type: String,
      default: ''
    },
    invalid: {
      type: Boolean,
      default: false
    }
  },
  // props: ['label', 'placeholder', 'inputId', 'invalid'],
  emits: ['emitTextInput'],
  data () {
    return {
      inputModel: this.model
    }
  },
  methods: {
    emitData () {
      this.$emit('emitTextInput', this.inputModel)
    }
  }
}
</script>

<style scoped>
  div {
    display: flex;
    flex-direction: column;
    max-width: 380px;
  }
  label, input {
    margin-bottom: 5px;
  }
  label {
    font-size: 15px;
    color: #293148;
    opacity: 45%;
    margin-bottom: 10px;
  }
  input {
    border-radius: 10px;
    border: 1px solid #E4E4E4;

    padding: 12px 16px;

    font-size: 16px;
  }
  input:focus{
    border: 1px solid #7895FF;
    outline: 2px solid #C9D5FF;
  }
  .danger-text-input {
    border: 2px solid #D90E32;
  }
</style>
