<template>
  <button :class="btnStyle" @click.prevent="$emit('action')">{{ text }}<img src="../assets/button/log_in.svg" alt="icon" v-if="showIcon"></button>
</template>

<script>
export default {
  props: ['text', 'show-icon', 'type'],
  emits: ['action'],
  computed: {
    btnStyle () {
      if (this.type === 'danger') {
        return 'danger'
      } else {
        return 'default'
      }
    }
  }
}
</script>

<style scoped>
  button {
    position: relative;

    border-radius: 10px;

    font-size: 14px;
    letter-spacing: 2px;
    line-height: 120%;
    font-weight: 700;
    text-transform: uppercase;

    max-width: 380px;
    padding: 14px 0;
  }
  img {
    position: absolute;
    right: 16px;
    top: 0;
    bottom: 0;
    margin: auto 0;
  }
  .danger {
    background-color: #D90E32;
    color: #fff;
    border: none;
  }
  .danger:hover {
    background-color: #DE304E;
  }
  .default {
    background-color: inherit;
    color: #E86E84;
    border: 1px solid #E86E84;
  }
  .default:hover {
    color: #D90E32;
    border: 1px solid #D90E32;
  }
</style>
