<template>
  <div class="profile_card">
    <div class="profile_card_person">
      <div class="person_ava">
        <img src="../assets/profileCard/user.png" alt="avatar" :class="toggleDefaultAva">
      </div>
      <div class="person_status_box">
        <span class="status_name">{{ data.username }}</span>
        <div class="status_other">
          <div class="status_other_photo">
            <img src="../assets/profileCard/camera.svg" alt="status">
            <a href="">Add photo</a>
          </div>
          <div class="status_other_status">
            <img :src="require(`../assets/profileCard/status-${data.status}.svg`)" alt="status">
            <span class="person_status" :class="`status-${data.status}`">{{ FirstLetterToUpCase(data.status) }}</span>
          </div>
        </div>
      </div>
    </div>
    <div class="profile_card_metrics">
      <div class="metrics_total">
        <span class="metrics_total_title">Total Credit</span>
        <span>{{ data.credits }}</span>
      </div>
      <div class="metrics_available">
        <span class="metrics_available_title">Available Tokens</span>
        <span>{{ data.tokens }}</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ['data'],
  computed: {
    toggleDefaultAva () {
      if (this.data.avatar === '') {
        return 'default_avatar'
      } else {
        return 'uploaded_avatar'
      }
    }
  },
  methods: {
    FirstLetterToUpCase (str) {
      return str[0].toUpperCase() + str.slice(1)
    }
  }
}
</script>
